import AppListNav from './appListNav.vue'
import * as AppResourceApi from '../../api/moudle/appResourceApi'
import UploadApp from './uploadApp.vue'
import {getTeamArr} from '../../mgr/userMgr'
import CollectionView from '../base/collectionView.vue'
import Bus from '../../common/js/bus'

export default {
  data() {
    return {
      currentPlatform: '',
      dataList: [],
      queryText: '',
      showUploadView: false,
      file: FileList,
      currentPage: 0,
      teamArr: [],
      busy: true
    }
  },
  components: {
    AppListNav, UploadApp, CollectionView
  },
  computed: {
  },
  methods: {
    loadAppList(isfooter) {
      if (isfooter) {
        this.currentPage = this.currentPage + 1
      } else {
        this.currentPage = 0
      }
      AppResourceApi.getAppList(this.teamArr[0]._id, this.currentPage)
        .then(response => {
          console.log(response)
          if (isfooter) {
            this.dataList = this.dataList.concat(response.data)
            if (response.data.length === 0) {
              this.busy = true
            } else {
              this.busy = false
            }
          } else {
            this.dataList = []
            this.dataList = response.data
            this.busy = false
          }
        }, reject => {
          this.$message.error(reject)
        })
    },
    loadMore() {
      this.busy = true
      setTimeout(() => {
        this.loadAppList(true)
      }, 800)
    },
    referenceUpload(e) {
      this.file = e.target.files
      if (e.target.files.length > 0) {
        this.showUploadView = true
      }
    },
    closeUploadMethod() {
      this.showUploadView = false
    },
    uploadSuccessMethod() {
      this.showUploadView = false
      this.loadAppList()
    },
    clickIosPlatform() {
      this.currentPlatform = 'ios'
    },
    clickAndroidPlatform() {
      this.currentPlatform = 'android'
    },
    getActiveClass(flag) {
      if (flag === this.currentPlatform) {
        return 'platformActive'
      }
    },
    gotoAppDetail(item) {
      this.$router.push({
        name: 'AppDetail',
        params: {appId: item._id}
      })
      Bus.$emit('appdetail')
    },
    appItemHovered() {
    },
    appItemUnhovered() {
    },
    clickEditorBtn() {
    },
    clickPreview() {
    },
    delectApp(item) {
      this.$confirm('确认删除？')
        .then(_ => {
          AppResourceApi.delectApp(this.teamArr[0]._id, item._id).then((res) => {
            this.loadAppList()
          }, reject => {
            this.$message.error(reject)
          })
        })
        .catch(_ => {})
    }
  },
  created () {
    console.log('applist')
    Bus.$emit('applist')
    this.$watch('queryText', () => {
      console.log(this.queryText)
    })
    this.teamArr = getTeamArr()
    if (this.teamArr) {
      this.loadAppList(false)
    }
  }
}



