import Bus from '../../common/js/bus'

export default {
  data() {
    return {
      slotName: 'main-nav'
    }
  },
  created() {
    Bus.$on('appdetail', () => {
      this.slotName = 'detail-nav'
    })
    Bus.$on('applist', () => {
      this.slotName = 'main-nav'
      this.$router.push('/apps')
    })
  },
  methods: {
    clickSubItem(data) {
      console.log(data.index)
      if (data.index === '应用概述') {
        Bus.$emit('appSummary')
      }
      if (data.index === '应用设置') {
        Bus.$emit('appSetting')
      }
    }
  }
}



