export default {
  props: {
    versionInfo: {
      type: Object
    }
  },
  data() {
    return {
      show: false,
      updataContent: '',
      showinDownLoadPage: false
    }
  },
  created() {
    setTimeout(() => {
      this.show = true
    }, 200)
  },
  methods: {
    cancel() {
      this.$emit('cancel')
    },
    sure() {

    },
    clickcontent() {

    },
    getDownLoadCount(count) {
      if (count) {
        return count
      } else {
        return 0
      }
    },
    getAllowDownLoadCount(strategy) {
      if (strategy.downloadCountLimit) {
        return strategy.downloadCountLimit
      } else {
        return '不限'
      }
    }
  }
}



